'use client';
import { memo, useCallback, useState } from 'react';
import { Handle, Position, NodeProps } from 'reactflow';
import { Type, ChevronDown, ChevronUp } from 'lucide-react';
import useStore from '@/store/useStore';
import { TextNodeData } from '@/types/workflow';

const TextNode = ({ id, data, selected }: NodeProps<TextNodeData>) => {
  const updateNodeData = useStore((state) => state.updateNodeData);
  const [isExpanded, setIsExpanded] = useState(true);

  const handleTextChange = useCallback(
    (e: React.ChangeEvent<HTMLTextAreaElement>) => {
      updateNodeData(id, { text: e.target.value });
    },
    [id, updateNodeData]
  );

  const handleLabelChange = useCallback(
    (e: React.ChangeEvent<HTMLInputElement>) => {
      updateNodeData(id, { label: e.target.value });
    },
    [id, updateNodeData]
  );

  return (
    <div
      className={`
        min-w-[280px] max-w-[320px] 
        bg-white dark:bg-slate-800 
        rounded-xl shadow-lg
        border-2 transition-all duration-200
        ${selected 
          ? 'border-blue-500 shadow-blue-500/20 shadow-xl' 
          : 'border-slate-200 dark:border-slate-700 hover:border-blue-300 dark:hover:border-blue-600'
        }
      `}
    >
      {/* Header */}
      <div className="px-4 py-3 bg-gradient-to-r from-blue-50 to-cyan-50 dark:from-blue-950/30 dark:to-cyan-950/30 border-b border-slate-200 dark:border-slate-700 rounded-t-xl">
        <div className="flex items-center gap-2 mb-2">
          <div className="w-7 h-7 rounded-full bg-blue-500 text-white flex items-center justify-center shadow-md">
            <Type size={14} />
          </div>
          <input
            type="text"
            value={data.label || 'Text Node'}
            onChange={handleLabelChange}
            className="flex-1 text-sm font-semibold bg-transparent border-none outline-none focus:ring-0 text-slate-800 dark:text-slate-100 placeholder-slate-400"
            placeholder="Text Node"
          />
          <button
            onClick={() => setIsExpanded(!isExpanded)}
            className="p-1 hover:bg-white/50 dark:hover:bg-slate-700/50 rounded transition-colors"
          >
            {isExpanded ? <ChevronUp size={16} /> : <ChevronDown size={16} />}
          </button>
        </div>
      </div>

      {/* Content */}
      {isExpanded && (
        <div className="p-4">
          <textarea
            value={data.text || ''}
            onChange={handleTextChange}
            placeholder="Enter text content..."
            className="
              w-full min-h-[120px] max-h-[300px]
              p-3 text-sm
              bg-slate-50 dark:bg-slate-900
              border border-slate-200 dark:border-slate-700
              rounded-lg
              focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent
              text-slate-700 dark:text-slate-200
              placeholder-slate-400
              resize-y
              transition-shadow
            "
          />
          <div className="mt-2 text-xs text-slate-400">
            {data.text?.length || 0} characters
          </div>
        </div>
      )}

      {/* Handles */}
      <Handle
        type="source"
        position={Position.Right}
        id="source"
        className="!w-3 !h-3 !bg-blue-500 !border-2 !border-white dark:!border-slate-800 hover:!w-4 hover:!h-4 transition-all"
      />
      <Handle
        type="target"
        position={Position.Left}
        id="target"
        className="!w-3 !h-3 !bg-blue-500 !border-2 !border-white dark:!border-slate-800 hover:!w-4 hover:!h-4 transition-all"
      />
    </div>
  );
};

export default memo(TextNode);
