import React, { useState } from 'react';
import { Type, Image as ImageIcon, Bot, LayoutGrid, ChevronLeft, ChevronRight, Save, Upload as UploadIcon, Undo, Redo } from 'lucide-react';
import useStore from '@/store/useStore';

const Sidebar = () => {
  const [collapsed, setCollapsed] = useState(false);
  const { undo, redo, saveWorkflow, loadWorkflow } = useStore();

  const onDragStart = (event: React.DragEvent, nodeType: string) => {
    event.dataTransfer.setData('application/reactflow', nodeType);
    event.dataTransfer.effectAllowed = 'move';
  };
  
  const handleFileUpload = (e: React.ChangeEvent<HTMLInputElement>) => {
      const file = e.target.files?.[0];
      if (!file) return;
      const reader = new FileReader();
      reader.onload = (evt) => {
          try {
              const flow = JSON.parse(evt.target?.result as string);
              loadWorkflow(flow);
          } catch (err) {
              console.error("Failed to parse workflow", err);
              alert("Invalid workflow file");
          }
      };
      reader.readAsText(file);
  };

  const handleSave = () => {
    saveWorkflow();
    // Trigger download
    const { nodes, edges } = useStore.getState();
    const flow = { nodes, edges };
    const blob = new Blob([JSON.stringify(flow, null, 2)], { type: 'application/json' });
    const url = URL.createObjectURL(blob);
    const link = document.createElement('a');
    link.href = url;
    link.download = `galaxy-workflow-${Date.now()}.json`;
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
  };

  return (
    <aside 
        className={`h-screen bg-slate-50 dark:bg-slate-900 border-r border-slate-200 dark:border-slate-800 transition-all duration-300 z-10 flex flex-col ${collapsed ? 'w-16' : 'w-64'}`}
    >
      <div className="p-4 border-b border-slate-200 dark:border-slate-800 flex items-center justify-between">
        {!collapsed && <h1 className="font-bold text-xl bg-gradient-to-r from-primary to-blue-600 bg-clip-text text-transparent">Galaxy.ai</h1>}
        <button 
            onClick={() => setCollapsed(!collapsed)}
            className="p-1 hover:bg-slate-200 dark:hover:bg-slate-800 rounded-md transition-colors text-slate-500"
        >
            {collapsed ? <ChevronRight size={18} /> : <ChevronLeft size={18} />}
        </button>
      </div>

      <div className="flex-1 overflow-y-auto p-4 space-y-6">
        {/* ... (rest of sidebar content) ... */}
        
        {/* Quick Access */}
        <div className="space-y-4">
            {!collapsed && <div className="text-xs font-semibold text-slate-400 uppercase tracking-wider">Quick Access</div>}
            
            <div 
                draggable 
                onDragStart={(event) => onDragStart(event, 'textNode')}
                className="flex items-center gap-3 p-3 bg-white dark:bg-slate-800 rounded-lg border border-slate-200 dark:border-slate-700 shadow-sm cursor-grab hover:border-primary hover:shadow-md transition-all group"
            >
                <div className="w-8 h-8 rounded-full bg-blue-100 dark:bg-blue-900/30 text-blue-600 flex items-center justify-center shrink-0">
                    <Type size={16} />
                </div>
                {!collapsed && (
                    <div className="flex-1">
                        <div className="text-sm font-medium">Text Node</div>
                        <div className="text-xs text-slate-500">Plain text input</div>
                    </div>
                )}
            </div>

            <div 
                draggable 
                onDragStart={(event) => onDragStart(event, 'imageNode')}
                className="flex items-center gap-3 p-3 bg-white dark:bg-slate-800 rounded-lg border border-slate-200 dark:border-slate-700 shadow-sm cursor-grab hover:border-primary hover:shadow-md transition-all"
            >
                 <div className="w-8 h-8 rounded-full bg-pink-100 dark:bg-pink-900/30 text-pink-600 flex items-center justify-center shrink-0">
                    <ImageIcon size={16} />
                </div>
                {!collapsed && (
                    <div className="flex-1">
                        <div className="text-sm font-medium">Image Node</div>
                        <div className="text-xs text-slate-500">Image upload</div>
                    </div>
                )}
            </div>

            <div 
                draggable 
                onDragStart={(event) => onDragStart(event, 'llmNode')}
                className="flex items-center gap-3 p-3 bg-white dark:bg-slate-800 rounded-lg border border-slate-200 dark:border-slate-700 shadow-sm cursor-grab hover:border-primary hover:shadow-md transition-all"
            >
                 <div className="w-8 h-8 rounded-full bg-violet-100 dark:bg-violet-900/30 text-violet-600 flex items-center justify-center shrink-0">
                    <Bot size={16} />
                </div>
                {!collapsed && (
                    <div className="flex-1">
                        <div className="text-sm font-medium">Run Gemini</div>
                        <div className="text-xs text-slate-500">Multimodal LLM</div>
                    </div>
                )}
            </div>
        </div>
      </div>

      {/* Control Panel (Bottom) */}
      <div className="p-4 border-t border-slate-200 dark:border-slate-800 bg-slate-50 dark:bg-slate-900">
        {!collapsed && <div className="text-xs font-semibold text-slate-400 uppercase tracking-wider mb-3">Controls</div>}
        <div className={`grid ${collapsed ? 'grid-cols-1' : 'grid-cols-2'} gap-2`}>
            <button onClick={undo} className="p-2 flex items-center justify-center gap-2 bg-white dark:bg-slate-800 border border-slate-200 dark:border-slate-700 rounded-md text-slate-600 hover:text-primary transition-colors" title="Undo">
                <Undo size={16} />
            </button>
            <button onClick={redo} className="p-2 flex items-center justify-center gap-2 bg-white dark:bg-slate-800 border border-slate-200 dark:border-slate-700 rounded-md text-slate-600 hover:text-primary transition-colors" title="Redo">
                <Redo size={16} />
            </button>
            <button onClick={handleSave} className="p-2 flex items-center justify-center gap-2 bg-white dark:bg-slate-800 border border-slate-200 dark:border-slate-700 rounded-md text-slate-600 hover:text-primary transition-colors" title="Save & Export">
                <Save size={16} />
                {!collapsed && <span className="text-xs">Save</span>}
            </button>
            <label className="p-2 flex items-center justify-center gap-2 bg-white dark:bg-slate-800 border border-slate-200 dark:border-slate-700 rounded-md text-slate-600 hover:text-primary transition-colors cursor-pointer" title="Load">
                <UploadIcon size={16} />
                {!collapsed && <span className="text-xs">Load</span>}
                <input type="file" className="hidden" accept=".json" onChange={handleFileUpload} />
            </label>
        </div>
      </div>
    </aside>
  );
};

export default Sidebar;
